<template>
  <div id="passage-auth-container" ref="containerRef">
    <div class="container" v-show="!initialLoading">
      <passage-router-view :key="appId" />
    </div>
  </div>
</template>
<script lang="ts">
import { useRouter } from '@/PassageAuth/composables/useRouter'
import { RegisterRoute } from '@/PassageAuth/router/routes'
import { defineComponent, PropType, onBeforeUpdate, ref, watch } from 'vue'
import { useAppId } from '@/composables/useAppId'
import { useWebFonts } from '@/composables/useWebFonts'
import { useCallbacks, BeforeAuthCallback, OnSuccessCallback } from '@/composables/useCallbacks'
import PassageRouterView from '@/PassageAuth/views/RouterView/RouterView.vue'
import appStyles from '../../assets/scss/app.scss'
import { useIntlTelInput } from '@/composables/useIntlTelInput'
import { setLocale } from '@/composables/useLocale'
import { useDefaultCountryCode } from '@/composables/useDefaultCountryCode'

export default defineComponent({
  name: 'RegisterApp',
  props: {
    appId: String,
    beforeAuth: Function as PropType<BeforeAuthCallback>,
    onSuccess: Function as PropType<OnSuccessCallback>,
    lang: String,
    defaultCountryCode: String,
  },
  components: {
    PassageRouterView,
  },
  styles: [appStyles],
  setup(props) {
    function storeProps() {
      const { updateAppId } = useAppId()
      if (props.appId) {
        updateAppId(props.appId)
      }

      const { updateDefaultCountryCode } = useDefaultCountryCode()
      updateDefaultCountryCode(props.defaultCountryCode)

      const { setBeforeAuth, setOnSuccess } = useCallbacks()
      setBeforeAuth(props.beforeAuth)
      setOnSuccess(props.onSuccess)
    }
    storeProps()

    onBeforeUpdate(() => {
      storeProps()
    })

    const initialLoading = ref<boolean>(true)
    setLocale(props.lang).finally(() => {
      initialLoading.value = false
    })
    watch(
      () => props.lang,
      (previous, next) => {
        if (previous !== next) {
          setLocale(props.lang)
        }
      },
    )

    const { router } = useRouter()
    router.resetRouter()
    router.homeRoute = RegisterRoute

    useWebFonts()

    const containerRef = ref()

    useIntlTelInput(containerRef)

    return {
      containerRef,
      initialLoading,
    }
  },
})
</script>
